.footer {
    .top {
        width: 100%;
        line-height: 100px;
        margin-top: 50px;
        margin-bottom: 30px;

        .iconCon {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
            width: 450px;
            margin: auto;

            div {
                text-align: center;
                color: white;

                .social{
                    cursor: pointer;
                    &:hover{
                        color: #C6C6C6;
                    }
                }
            }
        }
    }

    .bottom {
        background-color: #00000033;

        .head {
            line-height: 100px;
            width: 85%;
            margin: auto;

            .logo {
                width: 100px;
                height: auto;
            }

            .btCon {
                text-align: right;
            }

            .bt {
                width: 130px;
                height: 40px;
                background-color: white;
                margin: 15px;
                color: #0F55A7;
                text-transform: none;
                position: relative;

                .icon {
                    position: absolute;
                    left: 10px;
                }

                div {
                    font-size: 10px;
                    position: absolute;
                    top: 4px;
                    left: 45px;
                }

                section {
                    position: absolute;
                    top: 18px;
                    left: 45px;
                    font-size: 12px;
                    font-weight: 600;
                }
            }
        }

        .mid {
            width: 85%;
            margin: auto;
            padding: 20px;

            ul {
                padding: 0px;
                color: white;
                font-weight: 500;
                font-size: 17px;

                li {
                    list-style: none;
                    padding: 0px;
                    font-weight: 400;
                    font-size: 13px;
                    margin-top: 5px;
                }
            }

            .b2 {

                div {
                    right: 0px;
                    width: 75%;
                    margin-left: auto;
                    text-align: right;

                    textarea {
                        width: 100%;
                        height: 120px;
                        border-radius: 15px;
                        border: 1px solid rgba(255, 255, 255, 0.36);
                        background-color: transparent;
                        padding: 15px;
                        font-size: 13px;
                        color: white;
                    }

                    .send {
                        background-color: white;
                        color: #0F55A7;
                        margin-top: 10px;
                        text-transform: none;
                        width: 80px;
                        font-weight: 600;
                        font-size: 13px;
                    }
                }

            }
        }

        .bot {
            text-align: center;
            font-size: 12px;
            padding-top: 10px;
            padding-bottom: 30px;
            color: white;
            letter-spacing: .7px;
        }
    }
}

@media (max-width:992px) {
    .footer {
        .bottom {

            .mid {
                text-align: center;

                .b2 {
                    div {
                        margin: auto;
                        text-align: center;
                    }
                }

            }
        }
    }
}

@media (max-width:768px) {
    .footer {
        .bottom {
            .head {
                text-align: center;

                .btCon {
                    text-align: center;
                }
            }

        }
    }
}

@media (max-width:500px) {
    .footer {

        .top {
            .iconCon {
                width: 300px;
            }
        }


        .bottom {

            .mid {
                .b2 {
                    padding: 0px;

                    div {
                        width: 300px;
                        margin: auto;
    
                    }
                }
            }

            .btCon {
                padding: 0px;

                .bt {
                    margin: 10px;
                }
            }
        }
    }
}