.landingPageBar {
    .navbar {
        position: relative;
        top: 40px;
        text-align: center;

        .navbar-toggler {
            &:focus {
                box-shadow: none;
                border: 1px solid white;
            }
        }

        .navbar-brand {
            padding-left: 5%;
            color: white;

            &:hover {
                color: white;
            }

            h1 {
                font-size: 50px;
                font-weight: bolder;
                letter-spacing: 1px;
            }
        }

        .navbar-toggler-icon {
            display: grid;
            place-content: center;
            width: 30px;
            height: 25px;
            color: rgb(96, 183, 203);
        }

        .ul1 {
            margin-left: 50px;

            .nav-item {
                margin-left: 30px;

                a {
                    color: white;
                }
            }
        }

        .ul2 {
            margin-left: auto;
            margin-right: 60px;

            .nav-item {
                margin-right: 20px;

                .bt {
                    border-color: white;
                    color: white;
                    width: 100px;
                    font-size: 12px;
                    border-radius: 20px;
                    font-weight: 500;
                    letter-spacing: 1px;
                    text-transform: none;
                }
            }
        }
    }



    @media (max-width:1300px) {
        .navbar {
            .ul1 {
                margin-left: 30px;
            }

            .ul2 {
                margin-right: 30px;
            }
        }
    }

    @media (max-width:1000px) {
        .navbar {
            .ul1 {
                margin-left: 0px;

                .nav-item {
                    margin-left: 0px;
                    margin: 10px;
                }
            }

            .ul2 {
                margin-right: 0px;

                .nav-item {
                    margin-right: 0px;
                    margin: 10px;
                }
            }
        }
    }

}