.landingPage {
    width: 100%;
    background-color: #0b62c5;


    ///////////////////////// screen-1 /////////////////////////////////////////////////

    .screen1 {
        margin-top: 80px;

        .left {
            padding-left: 70px;
            color: white;

            .head1 {
                padding-top: 50px;
                font-size: 50px;
                font-weight: 800;
                letter-spacing: 1px;

                span {
                    color: #FADD3B;
                }
            }

            p {
                margin-top: 10px;
                max-width: 450px;
            }

            .but1 {
                width: 220px;
                height: 50px;
                background-color: white;
                border-radius: 30px;
                margin-top: 30px;
                box-shadow: 0px 21px 72px #00326C;
                position: relative;

                img {
                    width: 40px;
                    height: auto;
                    position: absolute;
                    left: 8px;
                    top: 5px;
                }

                .s {
                    position: absolute;
                    left: 55px;
                    top: 15px;
                    font-size: 14px;
                    color: #797979;
                }

                input {
                    width: 53%;
                    margin-left: 85px;
                    line-height: 23px;
                    margin-top: 14px;
                    padding-left: 4px;
                    outline: none;
                    border: none;
                    border-left: 2px solid #C6C6C6;
                    font-size: 12px;

                    &::placeholder {
                        color: #C6C6C6;
                    }
                }
            }

            .but2 {
                width: 220px;
                height: 50px;
                border-radius: 30px;
                margin-top: 30px;
                background: linear-gradient(176deg, #F1E57E 2.93%, #F1DC26 88.32%);
                box-shadow: 0px 21px 73px #002D63;
                color: #0F55A7;
                text-transform: none;
                font-weight: 700;
            }
        }

        .right {
            img {
                width: 100%;
                height: auto;
            }
        }
    }

    ///////////////////////// screen-2 /////////////////////////////////////////////////

    .screen2 {

        .left {
            img {
                width: 100%;
                height: auto;
            }
        }

        .right {
            padding-left: 50px;
            color: white;

            .head2 {
                padding-top: 100px;
                font-size: 50px;
                font-weight: 800;
                letter-spacing: 1px;

                span {
                    color: #FADD3B;
                }
            }

            p {
                max-width: 500px;
            }

            .p1 {
                margin-top: 30px;
            }

            .butCon {
                text-align: right;
                max-width: 480px;

                .but {
                    width: 180px;
                    height: 50px;
                    border-radius: 30px;
                    margin-top: 30px;
                    background: linear-gradient(176deg, #F1E57E 2.93%, #F1DC26 88.32%);
                    box-shadow: 0px 21px 73px #002D63;
                    color: #0F55A7;
                    text-transform: none;
                    font-weight: 700;
                    line-height: 18px;
                }
            }
        }

    }

    ///////////////////////// screen-3 /////////////////////////////////////////////////

    .screen3 {

        .left {
            padding-left: 70px;
            color: white;

            .head3 {
                padding-top: 100px;
                font-size: 50px;
                font-weight: 800;
                letter-spacing: 1px;

                span {
                    color: #FADD3B;
                }
            }

            p {
                margin-top: 10px;
                max-width: 450px;
            }

            .but3 {
                width: 220px;
                height: 50px;
                border-radius: 30px;
                margin-top: 30px;
                background: linear-gradient(176deg, #F1E57E 2.93%, #F1DC26 88.32%);
                box-shadow: 0px 21px 73px #002D63;
                color: #0F55A7;
                text-transform: none;
                font-weight: 700;
            }

        }

        .right {
            img {
                width: 100%;
                height: auto;
            }
        }
    }

}


@media (max-width:992px) {
    .landingPage {

        .screen1 {
            .left {
                padding-left: 0px;
                text-align: center;

                p {
                    margin: auto;
                    margin-top: 30px;
                }

                .but1 {
                    margin: auto;
                    margin-top: 30px;
                }
            }
        }

        .screen2 {
            .right {
                text-align: center;

                p {
                    margin: auto;
                }

                .butCon {
                    margin: auto;
                    text-align: center;
                }
            }
        }

        .screen3 {
            .left {
                text-align: center;

                p {
                    margin: auto;
                    margin-top: 30px;
                    // margin-bottom: 30px;
                }
            }
        }

    }
}


@media (max-width:500px) {
    .landingPage {
        .screen1 {

            .left {
                padding: 10px;

                .head1 {
                    padding-top: 0px;
                    font-size: 35px;
                }

                p {
                    padding: 10px;
                    font-size: 14px;
                }
            }

            .right {
                padding-top: 30px;
            }
        }

        .screen2 {
            .right {
                padding: 10px;

                .head2 {
                    padding-top: 50px;
                    font-size: 35px;
                }

                p {
                    font-size: 14px;
                }
            }

            .left {
                padding-top: 30px;
            }
        }

        .screen3 {
            .left {
                padding: 10px;

                .head3 {
                    padding-top: 50px;
                    font-size: 35px;
                }

                p {
                    font-size: 14px;
                }
            }

            .right {
                padding-top: 30px;
            }
        }
    }
}